import React from 'react';
import { Link } from 'react-router-dom';
import { Container, Carousel } from 'react-bootstrap';
import emailjs from 'emailjs-com';
import './style.css';

import landingA from '../../Assets/Images/landing1.jpg';
import featured1 from '../../Assets/Images/featured/featured1.jpg';
import InstagramIcon from '../../Assets/Icons/instagram.svg';
import FacebookIcon from '../../Assets/Icons/facebook.svg';

const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('gmail', 'template_lb69yn5', e.target, 'user_KnCSxYgOCrhOgC0adXpS8')
        .then((result) => {
            alert("Response successfuly sent!");
        }, (error) => {
            alert("Error, please try again later!");
        });

    e.target.reset();
}

const Landing = () => {
    return (
        <div className="landing">
            <div className="landing_info">
                <h1> Knits and Weaves Manufacturer </h1>
                <p> We make handmade and handknitted weaves, knits and embroideries </p>
                <Link to='/products'> View Products </Link>
            </div>
            <img src={landingA} className="landing_image" alt="" />
        </div >
    );
}

const Featuring = () => {
    return (
        <Container className="featuring">
            <h2>Featured Products</h2>
            <Carousel className="featuring_images">
                <Carousel.Item>
                    <img height="600" className="d-block w-100" src={featured1} alt="First slide" />
                </Carousel.Item>
                <Carousel.Item>
                    <img height="600" className="d-block w-100" src={featured1} alt="First slide" />
                </Carousel.Item>
                <Carousel.Item>
                    <img height="600" className="d-block w-100" src={featured1} alt="First slide" />
                </Carousel.Item>
            </Carousel>
        </Container>
    );
}

const Contact = () => {
    return (
        <Container id="contact" className="contact">
            <h2>Get in Touch</h2>
            <form className="contact_box" onSubmit={sendEmail}>
                <input placeholder="Name" required name="name"></input>
                <input placeholder="Email" required name="email"></input>
                <input placeholder="Subject" required name="subject"></input>
                <textarea placeholder="Message" required name="message"></textarea>
                <input id="submit_button" type="submit" value="Send" />
            </form>
        </Container>
    );
}

const Footer = () => {
    return (
        <div className="footer">

            <div className="social_profile">

                <a href="https://www.instagram.com/yalafashioninc/">
                    <img src={InstagramIcon} alt="instagram link" />
                </a>

                <a href="https://www.facebook.com/Yala-Fashions-Inc-2357220151164333">
                    <img src={FacebookIcon} alt="facebook link" />
                </a>

            </div>

            <h5> © Yala Fashion | 2020 </h5>
        </div>
    );
}


const Home = () => {
    return (
        <div className="home">
            <Landing />
            <div className="section_divider" />
            <Featuring />
            <div className="section_divider" />
            <Contact />
            <Footer />
        </div>
    );
};

export default Home;